body {
  background-color: rgb(72, 113, 136);
  color: white;
  font-family: Arial, sans-serif;
  margin: 0;
}

.App {
  text-align: center;
  max-width: 728px;
  margin: 0 auto;
}

.App header {
  background-color: #171718;
  height: 10vh;
  min-height: 50px;
  color: white;
  position: fixed;
  width: 100%;
  max-width: 728px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background-color: rgb(39, 83, 126);
  padding-top: 10vh; /* To avoid overlap with header */
  box-sizing: border-box;
}

main {
  padding: 10px;
  height: 80vh;
  margin: 10vh 0 10vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

main::-webkit-scrollbar {
  width: 0.25rem;
}

main::-webkit-scrollbar-track {
  background: #1e1e24;
}

main::-webkit-scrollbar-thumb {
  background: #6649b8;
}

form {
  height: 10vh;
  position: fixed;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  width: 100%;
  max-width: 728px;
  display: flex;
  font-size: 1.5rem;
  box-sizing: border-box;
}

form button {
  width: 20%;
  background-color: green;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
}

input {
  line-height: 1.5;
  width: 80%;
  font-size: 1.5rem;
  background: rgb(58, 58, 58);
  color: white;
  outline: none;
  border: none;
  padding: 0 10px;
  box-sizing: border-box;
}

button {
  background-color: rgb(204, 25, 25); 
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.sign-in {
  color: white;
  background: blue;
  max-width: 400px;
  margin: 0 auto;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
}

ul, li {
  text-align: left;
  list-style: none;
}

p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  text-align: left;
  display: inline-block;
}

.message {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.sent {
  flex-direction: row-reverse;
  text-align: right;
}

.sent p {
  background: #0b93f6;
  color: white;
  align-self: flex-end;
}

.received {
  text-align: left;
}

.received p {
  background: #e5e5ea;
  color: black;
}

img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 10px;
  object-fit: cover;
}
